@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root{
  --primary-color:#1aac83;
  --error:#e7195a;
}


body{
  background-color: #f1f1f1;
  margin: 0;
  font-family: 'Poppins', sans-serif;

}

header{
  background-color: #fff;

}
header .container{
  max-width: 1400px;
  margin:0 auto;
  padding: 10px 20px;
  display : flex;
  align-items: center;
  justify-content: space-between;

}
header a{
  color:#333;
  text-decoration: none;
}

.pages{
  max-width: 1400px;
  padding:20px;
  margin: 0 auto; 
}

 
.home{
  display:grid;
  grid-template-columns: 3fr 1fr;
   gap:100px;
}
.workout-details{
  background-color: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding:20px;
  position:relative;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.5);  
}

.workout-details h4{
  margin: 0 0 10px 0;
  font-size: 1.2rem;
  color: var(--primary-color);
}

.workout-details p{
  margin: 0;
  font-size: 0.9rem;
  color:#555;

}

.workout-details span{
  position: absolute;
  top: 20px;
  right:20px;
  cursor: pointer;
  background-color: #f1f1f1;
  padding: 8px;
  border-radius:50%;
  color: #333;
}

label, input{
  display: block;;
}
input{
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 1rem;
  font-family: 'Poppins', sans-serif;
}

button{
  background-color: var(--primary-color);
  border: 0;
  color: #fff;
  padding: 1rem;
  font-family: 'poppins', sans-serif;
  font-size: 1rem;
  cursor: pointer;
}
div.error{
   padding:10px;
  background-color: #ffefef;
  border: 1px solid var(--error);
  color: var(--error);
  border-radius: 4px;
  margin: 20px 0;
}

nav{
  display: flex;
  align-items: center;

}
nav a{
  margin-left: 10px;
}


nav button{
  background-color: #fff;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
  padding:6px 10px;
  border-radius: 4px;
  font-family:"Poppins" sans-serif;
  cursor: pointer;
  font-size: 1rem;
  margin-left: 12px; 
 }

form.signup, form.login{
max-width: 400px;
margin: 40px auto;
background-color: #fff;
border-radius: 4px;
padding: 25px;
}

